import React from 'react'

import Styles from './Subscribe.module.css'
import Container from './../../App.module.css'

export const Subscribe = () => {
    return (
        <div className={Styles.subscribe} id="subscribe">
            <div className={Container.container}>
                <div className={Styles.flexBlock}>
                    <div className={Styles.img}>
                        <h3 className={Styles.heading}>
                            Подпишитесь на нашу рассылку, чтобы быть в курсе обновлений 
                        </h3>
                    </div>
                    <form action="#" className={Styles.form}>
                        <label className={Styles.label} htmlFor="">Оставьте свой email</label>
                        <input className={Styles.input} placeholder="Введите email" type="email" id="email" name="email" />
                        <input className={Styles.button} type="submit" value="Отправить"/>
                    </form>
                </div>
            </div>
        </div>
    )
}