import React from 'react'

import Styles from './About.module.css'
import Container from './../../App.module.css'

export const About = () => {
    const elems = [
        { icon: 'personal_video', title: 'Панель управления' },
        { icon: 'storage', title: 'Хранение книг' },
        { icon: 'speaker_notes', title: 'Ведение заметок' }
    ]

    const items = elems.map(({icon, title}, i) => {
        return (
            <div key={ i } className={Styles.item}>
                <i className={`material-icons ${Styles.icon}`}>{ icon }</i>
                <h4 className={Styles.title}>
                    { title }
                </h4>
            </div>
        )
    })
    
    return (
        <div className={Styles.about} id="about">
            <div className={Container.container}>
                <h3 className={Styles.heading}>О нас</h3>
                <p className={Styles.text}>
                    Услуга, предназначенная для учеников и студентов учебных заведений Кыргызской Республики. Мы предоставляем онлайн-ресурсы, профессиональную поддержку и рекомендации всем нашим пользователям в любое время и из того места, где они решили учиться.
                </p>
                <div className={Styles.block}>
                    { items }
                </div>
            </div>
        </div>
    )
}