import React, { useState, useEffect, useCallback, useContext } from 'react'
import Styles from './Plan.module.css'

import { Agenda } from '../../Agenda/Agenda'
import { Form } from '../../Form/Form'
import { useHttp } from '../../../hooks/http.hook'
import { AuthContext } from '../../../context/AuthContext'

export const Plan = () => {
    const { userId, token } = useContext(AuthContext)
    const { loading, request } = useHttp()
    const [plan, setPlan] = useState([])
    const [opened, setOpened] = useState(false)

    const getData = useCallback(async () => {
        try {
            const fetched = await request(`/api/admin/panel/plan/${userId}`, "GET", null, {
                Authorization: `Bearer ${ token }`
            })
            setPlan(fetched)
        } catch (e) {}
    }, [token, userId, request])

    useEffect(() => {
        getData()
    }, [getData])

    const API_URL = "/api/admin/panel/plan/add"
    const route = "/admin/panel/plan"

    const elems = [
        {title: "Название", text: "name", type: "text"},
        {title: "Дата(с)", text: "dateFrom", type: "date", css: Styles.datetime},
        {title: "Дата(до)", text: "dateTo", type: "date" , css: Styles.datetime},
        {title: "Время(с)", text: "timeFrom", type: "time", css: Styles.datetime},
        {title: "Время(до)", text: "timeTo", type: "time", css: Styles.datetime}
    ]

    const openForm = () => {
        if (opened === false) {
            setOpened(true)
        } else {
            setOpened(false)
        }
    }

    return (
        <div className={Styles.plan}>
            <h2 className={Styles.heading}>
                План
                <a href="/" className={Styles.edit} onClick={e => {e.preventDefault(); openForm()}}>
                    <i className={`material-icons ${Styles.icon}`}>create</i>
                </a>
            </h2>
            { loading ? <div className={Styles.loading}></div> : plan && <Agenda plan={plan} />}
            { opened ? <Form openForm={openForm} elems={elems} api={API_URL} route={route} /> : <div></div> }
        </div>
    )
}