import React, { useState } from "react"
import Styles from "./Dashboard.module.css"
import { NavLink } from "react-router-dom"

export const Dashboard = () => { 
    const [open, setOpen] = useState(false)


    const menuOpen = event => {
        event.preventDefault()

        if (open === false){
            setOpen(true)
        } else {
            setOpen(false)
        }
    }

    const items = [
        { url: '/admin/panel/profile', text: 'Профиль', img: 'account_circle' },
        { url: '/admin/panel/plan', text: 'План', img: 'calendar_today' },
        { url: '/admin/panel/search', text: 'Поиск', img: 'search' },
        { url: '/admin/panel/notes', text: 'Заметки', img: 'notes' },
        { url: '/admin/panel/library', text: 'Библиотека', img: 'local_library' }
    ]

    const elems = items.map(({url, text, img}, i) => {
        return(
            <li key={ i } className={Styles.item}>
                <NavLink activeClassName={Styles.active} to={ url } className={`${Styles.link} ${open ? Styles.open : ""}`}>
                    <i className={`material-icons ${Styles.icon}`}>{ img }</i>
                    <span className={`${Styles.span} ${open ? Styles.open : ""}`}>{ text }</span>
                </NavLink>
            </li>
        )
    })

    return(
        <nav className={`${Styles.navbar} ${open ? Styles.open : ""}`}>
            <div className={Styles.flexBlock}>
                <div className={Styles.title}>
                <NavLink to="/admin/panel" className={`${Styles.logo} ${open ? Styles.open : ""}`}>Панель</NavLink>
                    <a href="/" className={Styles.ham} onClick={menuOpen}>
                        <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                        <span className={Styles.line}></span>
                        <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                    </a>
                </div>
                <ul className={Styles.menu}>
                    { elems }
                </ul>
            </div>
        </nav>
    )
}