import React from 'react'
import Styles from './Panel.module.css'
import { Switch, Route, Redirect } from "react-router-dom"

import { Dashboard } from '../../components/Panel/Dashboard/Dashboard'
import { Topline } from '../../components/Panel/Topline/Topline'

import { Profile } from '../../components/Scene/Profile/Profile'
import { Plan } from '../../components/Scene/Plan/Plan'
import { Search } from '../../components/Scene/Search/Search'
import { Notes } from '../../components/Scene/Notes/Notes'
import { Library } from '../../components/Scene/Library/Library'

export const Panel = ({ bestseller }) => {
    return (
        <div className={Styles.main}>
            <div className={Styles.nav}>
                <Dashboard />
            </div>
            <div className={Styles.interface}>
                <Topline />
                <div className={Styles.scene}>
                    <Switch>
                        <Route path="/admin/panel/profile">
                            <Profile />
                        </Route>
                        <Route path="/admin/panel/plan">
                            <Plan />
                        </Route>
                        <Route path="/admin/panel/search">
                            <Search bestseller={bestseller} />
                        </Route>
                        <Route path="/admin/panel/notes">
                            <Notes />
                        </Route>
                        <Route path="/admin/panel/library">
                            <Library />
                        </Route>
                        <Redirect to="/admin/panel/library" />
                    </Switch>
                </div>
            </div>
        </div>
    )
}