import React, { useState, useEffect, useCallback, useContext } from "react"
import styles from "./Login.module.css"

import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.min.css'
import { useError } from "./../../hooks/error.hook"
import { useSuccess } from './../../hooks/success.hook'
import { AuthContext } from './../../context/AuthContext'
import { useHttp } from "../../hooks/http.hook"

export const Login = () => {
    toast.configure({
        autoClose: 3000,
        draggable: true
    })

    const auth = useContext(AuthContext)
    const errorMessage = useError()
    const successMessage = useSuccess()
    const { loading, request, error, clearError } = useHttp()
    const [form, setForm] = useState({
        email: "",
        password: ""
    })

    useEffect(() => {
        errorMessage(error)
        clearError()
    }, [error, errorMessage, clearError])

    const changeHandler = event => {
        setForm({ ...form, [event.target.name]: event.target.value })
    }

    const registerHandler = async (e) => {
        e.preventDefault()
        try {
            const data = await request("/api/auth/register", "POST", { ...form })
            successMessage(data.message)
        } catch (e) {}
    }
    
    // eslint-disable-next-line
    const loginHandler = async (e) => {
        try {
            const data = await request("/api/auth/login", "POST", { ...form })
            auth.login(data.token, data.userId)
        } catch (e) {}
    }

    const enterHandler = useCallback((event) => {
        if(event.keyCode === 13) {
            loginHandler()
        }
    }, [loginHandler])

    useEffect(() => {
        document.addEventListener("keydown", enterHandler, false)
        return () => {
            document.removeEventListener("keydown", enterHandler, false)
        }
    }, [enterHandler])

    return(
        <div className={styles.login}>
            <div className={styles.block}>
                <h2 className={styles.heading}>Вход</h2>
                <form action="#" className={styles.form}>
                    <div className={styles.inputBlock}>
                        <input 
                            type="text"
                            className={styles.input}
                            name="email"
                            placeholder="Логин"
                            autoComplete="off"
                            onChange={changeHandler} />
                        <label htmlFor="email" className={styles.label}>Логин</label>
                    </div>
                    <div className={styles.inputBlock}>
                        <input 
                            type="password"
                            className={styles.input}
                            name="password"
                            placeholder="Пароль"
                            autoComplete="off"
                            onChange={changeHandler} />
                        <label htmlFor="password" className={styles.label}>Пароль</label>
                    </div>
                    <div className={loading ? styles.loading : styles.buttons}>
                        <a 
                            href="/" 
                            className={loading ? 
                                styles.dn : styles.submit}
                            onClick={loginHandler}>Войти</a>
                        <a 
                            href="/" 
                            className={loading ? 
                                styles.dn : styles.submit}
                            onClick={registerHandler}>Регистрация</a>
                    </div>
                </form>
            </div>
        </div>
    )
}