import React, { useState, useEffect, useContext } from "react"
import Styles from "./Form.module.css"

import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.min.css'
import { useHttp } from '../../hooks/http.hook'
import { useError } from '../../hooks/error.hook'
import { useSuccess } from '../../hooks/success.hook'
import { AuthContext } from '../../context/AuthContext'
import { useHistory } from "react-router-dom"

export const Form = ({ openForm, elems, api, route }) => {
    toast.configure({
        autoClose: 3000,
        draggable: true
    })

    const history = useHistory()
    const { token } = useContext(AuthContext)
    const { loading, request, error, clearError } = useHttp()
    const errorMessage = useError()
    const successMessage = useSuccess()
    const [form, setForm] = useState({})

    useEffect(() => {
        errorMessage(error)
        clearError()
    }, [error, errorMessage, clearError])

    const changeHandler = e => {
        setForm({ ...form, [e.target.name]: e.target.value })
    }

    const changeProfile = async (e) => {
        e.preventDefault()
        try {
            const data = await request(api, "POST", { ...form }, {
                Authorization: `Bearer ${ token }`
            })
            successMessage(data.message)
            history.push("/admin/panel")
            history.push(route)
        } catch (e) {}
    }

    const items = elems.map(({title, text, type, warn, css}, i) => {
        return(
            <div key={ i } className={`${Styles.inputBlock} ${ css }`}>
                <b className={Styles.label}>{ title }</b>
                <input 
                    type={ type }
                    className={Styles.input}
                    name={ text }
                    placeholder={ title }
                    autoComplete="off"
                    onChange={changeHandler} />
                { warn }
            </div>
        )
    })

    return(
        <div className={Styles.block}>
            <div className={Styles.overlay} onClick={() => openForm()}></div>
            <form className={Styles.form}>
                { items }
                <div className={Styles.center}>
                    <div className={loading ? Styles.loading : Styles.buttons}>
                        <a href="/" className={loading ? Styles.dn : Styles.cancel} onClick={e => {e.preventDefault(); openForm()}}>Отменить</a>
                        <a href="/" className={loading ? Styles.dn : Styles.save} onClick={changeProfile}>Сохранить</a>
                    </div>
                </div>
            </form>
        </div>
    )
}