import React from 'react'
import Styles from './Topline.module.css'

import { BrowserRouter as Router } from "react-router-dom"
import { HashLink as Link } from 'react-router-hash-link'

export const Topline = () => {
    return (
        <div className={Styles.topline} id="main">
            <div className={Styles.block}>
                <div className={Styles.arrow}>
                    <h2 className={Styles.heading}>
                        Сервис для хранения и использования <br/> книг онлайн
                    </h2>
                    <Router>
                        <Link to="/#about" className={Styles.link}>
                            <i className={`material-icons ${Styles.icon}`}>keyboard_arrow_down</i>
                        </Link>
                    </Router>
                    {/* <a href="/" className={Styles.link}>
                        <img src={Arrow} alt="arrow" className={Styles.img} />
                    </a> */}
                </div>
            </div>
        </div>
    )
}