import React, { useState, useCallback, useEffect } from 'react'
import Styles from './Search.module.css'

import { useHttp } from '../../../hooks/http.hook'
import { Books } from '../../Books/Books'
import { Bestseller } from '../../Bestseller/Bestseller'

export const Search = ({ bestseller }) => {
    const { loading, request } = useHttp()
    const [books, setBooks] = useState([])

    const [form, setForm] = useState("")

    const getData = useCallback(async () => {
        try {
            const fetched = await request(`https://www.googleapis.com/books/v1/volumes?q=${form}`, "GET", null)
            setBooks(fetched.items)
        } catch (e) {}
    }, [form, request])

    const changeHandler = ({ currentTarget = {} }) => {
        const { value } = currentTarget
        setForm(value)
    }

    const body = books.map(({volumeInfo}) => {
        return volumeInfo
    })

    const relewance = bestseller.map(({volumeInfo}) => {
        return volumeInfo
    })

    const enterHandler = useCallback((event) => {
        if(event.keyCode === 13) {
            getData()
        }
    }, [getData])

    useEffect(() => {
        document.addEventListener("keydown", enterHandler, false)
        return () => {
            document.removeEventListener("keydown", enterHandler, false)
        }
    }, [enterHandler])

    return (
        <div className={Styles.search}>
            <h2 className={Styles.heading}>
                Поиск
            </h2>
            <div className={Styles.find}>
                <input className={Styles.input} name="title" type="text" placeholder="Найти" onChange={changeHandler} />
                <a href="#" onClick={e => {e.preventDefault(); getData()}}>
                    <i className={`material-icons ${Styles.icon}`}>search</i>
                </a>
            </div>
            { loading ? <div className={Styles.loading}></div> : body && <Books books={body} />}
            { relewance && <Bestseller bestseller={relewance} />}
        </div>
    )
}