import React, { useState, useContext, useCallback, useEffect } from 'react'
import Styles from './Profile.module.css'

import { Form } from '../../Form/Form'
import { AuthContext } from '../../../context/AuthContext'
import { useHttp } from '../../../hooks/http.hook'

export const Profile = () => {
    const { userId, token } = useContext(AuthContext)
    const { loading, request } = useHttp()
    const [profile, setProfile] = useState([])
    const [opened, setOpened] = useState(false)

    const getData = useCallback(async () => {
        try {
            const fetched = await request(`/api/admin/panel/profile/${userId}`, "GET", null, {
                Authorization: `Bearer ${ token }`
            })
            setProfile(fetched)
        } catch (e) {}
    }, [userId, request])

    useEffect(() => {
        getData()
    }, [getData])

    const array = profile.length !== 0 ? 
        profile.slice(-1)[0] : {
            name: "---", 
            surname: "---", 
            email: "---", 
            status: "---"
    }
    
    const API_URL = "/api/admin/panel/profile/add"
    const route = "/admin/panel/profile"

    const elems = [
        { title: 'Имя', prev: array.name, text: "name", type: "text" },
        { title: 'Фамилия', prev: array.surname, text: "surname", type: "text" },
        { title: 'Email', prev: array.email, text: "email", type: "email" },
        { title: 'Статус', prev: array.status, text: "status", type: "text", warn: <b className={Styles.warn}>Школьник, Студент и др.</b> }
    ]

    const items = elems.map(({title, prev}, i) => {
        return (
            <div key={ i } className={Styles.item}>
                <h3 className={Styles.title}>
                    { title }
                </h3>
                <p className={Styles.text}>
                    { prev }
                </p>
            </div>
        )
    })

    const openForm = () => {
        if (opened === false) {
            setOpened(true)
        } else {
            setOpened(false)
        }
    }

    if (loading) {
        return <div className={Styles.loading}></div>
    } else {
        return (
            <div className={Styles.center}>
                <div className={Styles.profile}>
                    <h2 className={Styles.heading}>
                        Профиль
                        <a href="/" className={Styles.edit} onClick={e => {e.preventDefault(); openForm()}}>
                            <i className={`material-icons ${Styles.icon}`}>create</i>
                        </a>
                    </h2>
                    <div className={Styles.block}>
                        { items }
                    </div>
                </div>
                { opened ? <Form openForm={openForm} elems={elems} api={API_URL} route={route} /> : <div></div> }
            </div>
        )
    }
}