import React, { useState, useCallback, useEffect } from 'react'
import Styles from './Notes.module.css'

export const Notes = () => {
    const [form, setForm] = useState("")
    const changeHandler = ({ currentTarget = {} }) => {
        const { value } = currentTarget
        setForm(value)
    }

    const saveNotes = () => {
        const storageName = "notes"
        localStorage.setItem(storageName, form)
    }

    const enterHandler = useCallback((event) => {
        if(event.keyCode === 13) {
            saveNotes()
        }
    }, [saveNotes])

    useEffect(() => {
        document.addEventListener("keydown", enterHandler, false)
        return () => {
            document.removeEventListener("keydown", enterHandler, false)
        }
    }, [enterHandler])

    return (
        <div className={Styles.notes}>
            <h2 className={Styles.heading}>Заметки</h2>
            <div className={Styles.block}>
                <textarea className={Styles.textarea} onChange={changeHandler}></textarea>
                <div className={Styles.field}>
                    <p>
                        { form }
                    </p>
                </div>
            </div>
            <div className={Styles.save}>
                <a href="/" className={Styles.link} onClick={e => {e.preventDefault(); saveNotes()}}>
                    Сохранить
                </a>
            </div>
        </div>
    )
}