import React, { useState } from "react"

import Styles from "./Header.module.css"
import Container from "./../../App.module.css"

import { BrowserRouter as Router } from "react-router-dom"
import { HashLink as Link } from 'react-router-hash-link'
import { NavLink } from "react-router-dom"

export const Header = () => {
    const [opened, setOpened] = useState(false)

    const menuOpen = () => {
        if (opened === false) {
            setOpened(true)
        } else {
            setOpened(false)
        }
    }

    const items = [
        { id: 1, name: "Главная", url: "/#main" },
        { id: 2, name: "О нас", url: "/#about" },
        { id: 3, name: "Контакты", url: "/#contacts" }
    ]

    const elems = items.map(({id, name, url}) => {
        return <li key={id} className={`${Styles.item} ${opened ? Styles.opened : ""}`}>
            <Link to={url} className={Styles.navlink} onClick={menuOpen}>{name}</Link>
        </li>
    })

    return(
        <header className={Styles.header}>
            <div className={Container.container}>
                <div className={Styles.flexBlock}>
                    <div className={Styles.logo}>
                        <Router>
                            <Link to="/#topline" className={Styles.link}>
                                Онлайн-библиотека
                            </Link>
                        </Router>
                    </div>
                    <ul className={`${Styles.menu} ${opened ? Styles.opened : ""}`}>
                        <Router>
                            { elems }
                        </Router>
                    </ul>
                    <div className={Styles.right}>
                        <div className={Styles.admin}>
                            <NavLink to="/admin" className={Styles.link}>
                                <i className={`material-icons ${Styles.icon}`}>account_circle</i>
                            </NavLink>
                        </div>
                        <div className={Styles.hamburger} onClick={menuOpen}>
                            <span className={`${Styles.line} ${opened ? Styles.opened : ""}`}></span>
                            <span className={`${Styles.line} ${opened ? Styles.opened : ""}`}></span>
                            <span className={`${Styles.line} ${opened ? Styles.opened : ""}`}></span>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    )
}