import React, { useState } from 'react'

import Styles from './Library.module.css'
import pdf from './../../../assets/JavaScript.pdf'
import thumbnail from './../../../assets/images/thumbnail.JPG'

import FileViewer from 'react-file-viewer';

export const Library = () => {
    const [opened, setOpened] = useState(false)
    const books = [
        { title: 'JavaScript для детей', authors: ['Ник Морган'], publishedDate: '2016', thumbnail: thumbnail }
    ]

    const openPreview = () => {
        if (opened === false) {
            setOpened(true)
        } else {
            setOpened(false)
        }
    }

    const items = books.map(({title, authors, publishedDate, thumbnail}, i) => {
        return (
            <div key={ i } className={Styles.item} onClick={openPreview}>
                {
                    thumbnail === undefined ?
                    <i className={`material-icons ${Styles.icon}`}>block</i> :
                    <img className={Styles.thumbnail} src={ thumbnail } alt={ title } />

                }
                <p className={Styles.title}>
                    {title === undefined ? <span><b>Название:</b> ---</span> : <span><b>Название:</b> {title}</span>} <br/>
                    {authors === undefined ? <span><b>Авторы:</b> ---</span> : <span><b>Авторы:</b> {authors.join(', ')}</span>} <br/>
                    {publishedDate === undefined ? <span><b>Дата выпуска:</b> ---</span> : <span><b>Дата выпуска:</b> {publishedDate}</span>}
                </p>
                <a href="/" className={Styles.link} onClick={e => {e.preventDefault()}}>
                    <i className={`material-icons ${Styles.bookmark}`}>bookmark</i>
                </a>
            </div>
        )
    })

    return (
        <div className={Styles.library}>
            <h2 className={Styles.heading}>Библиотека</h2>
            <div className={Styles.block}>
                { items }
                {   opened ?
                    <div className={Styles.overlay}>
                        <a href="/" className={Styles.button} onClick={e => {e.preventDefault(); openPreview()}}>
                            <i className={`material-icons ${Styles.cancel}`}>clear</i>
                        </a>
                        <FileViewer
                            fileType='pdf'
                            filePath={pdf}
                            // errorComponent={CustomErrorComponent}
                            // onError={this.onError}
                        />
                    </div> :
                    <div></div>
                }
            </div>
        </div>
    )
}